/* Animation border */

.animate-border {
    position: relative;
    display: block;
    width: 115px;
    height: 3px;
    background: #2608AB;
}
  
.animate-border:after {
    position: absolute;
    content: "";
    width: 35px;
    height: 3px;
    left: 0;
    bottom: 0;
    border-left: 20px solid #fff;
    animation: animborder 3s linear infinite;
  }
  
@keyframes animborder {
    0% {
        transform: translateX(0px);
    }
    100% {
        transform: translateX(81px);
    }
}
