/* Dropdown transition styles */
.dropdown-enter {
  max-height: 0;
  opacity: 0;
  overflow: hidden;
  transition: max-height 0.3s ease-out, opacity 0.3s ease-out;
}

.dropdown-enter-active {
  max-height: 1000px; /* Adjust to a value that is sufficient for your content */
  opacity: 1;
}

.dropdown-exit {
  max-height: 1000px; /* Adjust to a value that is sufficient for your content */
  opacity: 1;
  overflow: hidden;
  transition: max-height 0.3s ease-in, opacity 0.3s ease-in;
}

.dropdown-exit-active {
  max-height: 0;
  opacity: 0;
}

